import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import Select from "react-select";
import makeAnimated from "react-select/animated";

import ErrorMsg from "../../ErrorMsg/ErrorMsg";
import LoadingComponent from "../../LoadingComp/LoadingComponent";
import SuccessMsg from "../../SuccessMsg/SuccessMsg";
import {fetchProductAction, updateProductAction} from "../../../redux/slices/product/productSlices" 
import { fetchCategoriesAction } from "../../../redux/slices/category.js/categorySlice";
import { fetchBrandsAction } from "../../../redux/slices/brand.js/brandSlice";
import { fetchColorsAction } from "../../../redux/slices/color.js/colorSlice";
import { useParams } from "react-router-dom";

//animated components for react-select
const animatedComponents = makeAnimated();

export default function ProductUpdate() {
  // dispatch
  const dispatch = useDispatch();
  // useParams
  const {id} = useParams();
  useEffect(() =>{
    dispatch(fetchProductAction(id))
  },[id, dispatch])

  // sizes
  const sizes = ["S", "M", "L", "XL", "XXL"]
  // const [sizeOption, setSizeOption] = useState([])
  const handleSizeChange = (sizes) =>{
    setSizeOption(sizes)
  }
  // converted Size
  const sizeOptionsCoverted = sizes?.map((size) =>{
    return {
      value: size,
      label: size,
    }
  });

  // categories
  useEffect(() => {
    dispatch(fetchCategoriesAction());
  }, [dispatch]);

  // select categories from store
  const {categories} =useSelector(
    (state) => state?.categories?.categories
  );

    // brands
  useEffect(() => {
    dispatch(fetchBrandsAction());
  }, [dispatch]);

  // select brands from store
  const {brands: {brands}} =useSelector(
    (state) => state?.brands
  );

  // colors
  // const [colorOption, setColorOption] = useState([])
// 
  useEffect(() => {
    dispatch(fetchColorsAction());
  }, [dispatch]);

  // select brands from store
  const {colors: {colors}} =useSelector(
    (state) => state?.colors
  );
  const colorOptionsCoverted = colors?.map((color) =>{
    return {
      value: color?.name,
      label: color?.name,
    }
  });
  // get data from store
  const {product, isUpdated , loading, error} = useSelector((state) => state.products)
  console.log(product);
  


  // const handleColorChange = (colors) =>{
  //   setColorOption(colors)
  // }

  //---form data---
  // const [formData, setFormData] = useState({
  //   name: product?.product?.name,
  //   description: product?.product?.description,
  //   category: "",
  //   sizes: "",
  //   brand: "",
  //   colors: "",
  //   price: product?.product?.price,
  //   totalQty: product?.product?.totalQty,
  // });

  const [formData, setFormData] = useState({
    name: "",
    description: "",
    category: "",
    brand: "",
    price: "",
    totalQty: "",
  });
  const [sizeOption, setSizeOption] = useState([]);
  const [colorOption, setColorOption] = useState([]);

   // Hydrate form when product arrives
  // useEffect(() => {
  //   if (product?.product?._id) {
  //     setFormData({
  //       name: product.product.name || "",
  //       description: product.product.description || "",
  //       category: product.product.category || "",
  //       brand: product.product.brand || "",
  //       price: product.product.price ?? "",
  //       totalQty: product.product.totalQty ?? "",
  //     });

  //     setSizeOption(
  //       product.product.sizes?.map((size) => ({ value: size, label: size })) || []
  //     );

  //     setColorOption(
  //       product.product.colors?.map((color) => ({ value: color, label: color })) || []
  //     );
  //   }
  // }, [product]);

  useEffect(() => {
  if (product?.product?._id) {
    setFormData({
      name: product.product.name || "",
      description: product.product.description || "",
      category: product.product.category?.[0] || "", // first category
      brand: product.product.brand || "",
      price: product.product.price ?? "",
      totalQty: product.product.totalQty ?? "",
    });

    // sizes
    setSizeOption(
      product.product.sizes?.map((size) =>
        typeof size === "string"
          ? { value: size, label: size }
          : { value: size.name, label: size.name } // if it's an object
      ) || []
    );

    // colors
    setColorOption(
      product.product.colors?.map((color) =>
        typeof color === "string"
          ? { value: color, label: color }
          : { value: color.name, label: color.name } // if it's an object
      ) || []
    );
  }
}, [product]);

  //onChange
  const handleOnChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });    
  };

  //onSubmit
  // const handleOnSubmit = (e) => {    
  //   e.preventDefault();
    
  //   dispatch(
  //     updateProductAction({
  //       ...formData,
  //       id: product?.product?._id, // must include id
  //       colors: colorOption?.map((color) => color?.label),
  //       sizes: sizeOption?.map((size) => size?.label)
  //     })
      
  //   );
  //   // console.log(colorOption);
    
  //   //reset form data
  //   setFormData({
  //     name: "",
  //     description: "",
  //     category: "",
  //     sizes: "",
  //     brand: "",
  //     colors: "",
  //     images: "",
  //     price: "",
  //     totalQty: "",
  //   });
    
  // };

   const handleOnSubmit = (e) => {
    e.preventDefault();
    dispatch(
      updateProductAction({
        ...formData,
        id: product?.product?._id, // must include product id
        colors: colorOption.map((color) => color.value),
        sizes: sizeOption.map((size) => size.value),
      })
    );
  };

  return (
    <>
      {error && <ErrorMsg message={error?.message} />}
      {isUpdated && <SuccessMsg message="Product Updated Successfully" />}
      <div className="flex min-h-full flex-col justify-center py-12 sm:px-6 lg:px-8">
        <div className="sm:mx-auto sm:w-full sm:max-w-md">
          <h2 className="mt-6 text-center text-3xl font-bold tracking-tight text-gray-900">
            Update Product
          </h2>
          <p className="mt-2 text-center text-sm text-gray-600">
            <p className="font-medium text-indigo-600 hover:text-indigo-500">
              Manage Products
            </p>
          </p>
        </div>

        <div className="mt-8 sm:mx-auto sm:w-full sm:max-w-md">
          <div className="bg-white py-8 px-4 shadow sm:rounded-lg sm:px-10">
            <form className="space-y-6" onSubmit={handleOnSubmit}>
              <div>
                <label className="block text-sm font-medium text-gray-700">
                  Product Name
                </label>
                <div className="mt-1">
                  <input
                    name="name"
                    value={formData?.name}
                    onChange={handleOnChange}
                    className="block w-full appearance-none rounded-md border border-gray-300 px-3 py-2 placeholder-gray-400 shadow-sm focus:border-indigo-500 focus:outline-none focus:ring-indigo-500 sm:text-sm"
                  />
                </div>
              </div>
              {/* size option */}
              <div>
                <label className="block text-sm font-medium text-gray-700">
                  Select Size
                </label>
                {/* <Select
                  components={animatedComponents}
                  isMulti
                  name="sizes"
                  options={sizeOptionsCoverted}
                  className="basic-multi-select"
                  classNamePrefix="select"
                  isClearable={true}
                  isLoading={false}
                  isSearchable={true}
                  closeMenuOnSelect={false}
                  onChange={(item) => handleSizeChange(item)}
                /> */}
                <Select
                  isMulti
                  options={sizeOptionsCoverted}   // available options
                  value={sizeOption}              // selected values
                  onChange={setSizeOption}        // updates state
                />

              </div>
              {/* Select category */}
              <div>
                <label className="block text-sm font-medium text-gray-700">
                  Select Category
                </label>
                <select
                  name="category"
                  value={formData.category}
                  onChange={handleOnChange}
                  className="mt-1  block w-full rounded-md border-gray-300 py-2  pl-3 pr-10 text-base focus:border-indigo-500 focus:outline-none focus:ring-indigo-500 sm:text-sm border"
                  defaultValue="Canada">
                  {/* <option>-- Select Category --</option>
                  <option value="Clothings">Clothings</option>
                  <option value="Shoes">Shoes</option>
                  <option value="Accessories">Accessories</option> */}
                  <option>-- Select Category --</option>
                  {categories?.map((category) => (
                    <option key={category?._id} value={category?.name}>
                      {category.name}
                    </option>
                  ))}
                </select>
              </div>
              {/* Select Brand */}
              <div>
                <label className="block text-sm font-medium text-gray-700">
                  Select Brand
                </label>
                <select
                  name="brand"
                  value={formData.brand}
                  onChange={handleOnChange}
                  className="mt-1  block w-full rounded-md border-gray-300 py-2  pl-3 pr-10 text-base focus:border-indigo-500 focus:outline-none focus:ring-indigo-500 sm:text-sm border"
                  defaultValue="Canada">
                  <option>-- Select Brand --</option>
                  {brands?.map((brand) => (
                    <option key={brand?._id} value={brand?.name}>
                      {brand.name}
                    </option>
                  ))}
                </select>
              </div>

              {/* Select Color */}
              <div>
                <label className="block text-sm font-medium text-gray-700">
                  Select Color
                </label>
                {/* <Select
                  components={animatedComponents}
                  isMulti
                  name="colors"
                  options={colorOptionsCoverted}
                  className="basic-multi-select"
                  classNamePrefix="select"
                  isClearable={true}
                  isLoading={false}
                  isSearchable={true}
                  closeMenuOnSelect={false}
                  onChange={(e) => handleColorChange(e)}
                /> */}

                <Select
                  isMulti
                  options={colorOptionsCoverted}
                  value={colorOption}
                  onChange={setColorOption}
                />

              </div>

              {/* upload images
              <div className="sm:grid sm:grid-cols-3 sm:items-start sm:gap-4 sm:border-t sm:border-gray-200 sm:pt-5">
                <label
                  htmlFor="cover-photo"
                  className="block text-sm font-medium text-gray-700 sm:mt-px sm:pt-2">
                  Upload Images
                </label>
                <div className="mt-1 sm:col-span-2 sm:mt-0">
                  <div className="flex max-w-lg justify-center rounded-md border-2 border-dashed border-gray-300 px-6 pt-5 pb-6">
                    <div className="space-y-1 text-center">
                      <svg
                        className="mx-auto h-12 w-12 text-gray-400"
                        stroke="currentColor"
                        fill="none"
                        viewBox="0 0 48 48"
                        aria-hidden="true">
                        <path
                          d="M28 8H12a4 4 0 00-4 4v20m32-12v8m0 0v8a4 4 0 01-4 4H12a4 4 0 01-4-4v-4m32-4l-3.172-3.172a4 4 0 00-5.656 0L28 28M8 32l9.172-9.172a4 4 0 015.656 0L28 28m0 0l4 4m4-24h8m-4-4v8m-12 4h.02"
                          strokeWidth={2}
                          strokeLinecap="round"
                          strokeLinejoin="round"
                        />
                      </svg>
                      
                      <p className="text-xs text-gray-500">
                        PNG, JPG, GIF up to 1MB
                      </p>
                    </div>
                  </div>
                </div>
              </div> */}

              {/* price */}
              <div>
                <label className="block text-sm font-medium text-gray-700">
                  Price
                </label>
                <div className="mt-1">
                  <input
                    name="price"
                    value={formData.price}
                    onChange={handleOnChange}
                    type="number"
                    className="block w-full appearance-none rounded-md border border-gray-300 px-3 py-2 placeholder-gray-400 shadow-sm focus:border-indigo-500 focus:outline-none focus:ring-indigo-500 sm:text-sm"
                  />
                </div>
              </div>

              {/* Quantity */}
              <div>
                <label className="block text-sm font-medium text-gray-700">
                  Total Quantity
                </label>
                <div className="mt-1">
                  <input
                    name="totalQty"
                    value={formData.totalQty}
                    onChange={handleOnChange}
                    type="number"
                    className="block w-full appearance-none rounded-md border border-gray-300 px-3 py-2 placeholder-gray-400 shadow-sm focus:border-indigo-500 focus:outline-none focus:ring-indigo-500 sm:text-sm"
                  />
                </div>
              </div>
              {/* description */}
              <div>
                <label
                  htmlFor="comment"
                  className="block text-sm font-medium text-gray-700">
                  Add Product Description
                </label>
                <div className="mt-1">
                  <textarea
                    rows={4}
                    name="description"
                    value={formData.description}
                    onChange={handleOnChange}
                    className="block w-full rounded-md border-gray-300 border shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm"
                  />
                </div>
              </div>
             <div>
  {loading ? (
    <LoadingComponent />
  ) : (
    <button
      type="submit"
      className="flex w-full justify-center rounded-md border border-transparent bg-indigo-600 py-2 px-4 text-sm font-medium text-white shadow-sm hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:ring-offset-2"
    >
      Update Product
    </button>
  )}
</div>

            </form>
          </div>
        </div>
      </div>
    </>
  );
}
